html {
  color: #212121;
  background-color: #fff;
  font-family: verdana, arial, sans-serif;
  font-size: 22px;
  line-height: 1.3;
}

turbo-frame {
  display: block;
}

input, textarea, select, button {
  font-size: 100%;
}

a {
  color: #ec005a;
}

.turbo-progress-bar {
  background-color: #ec005a;
}

.products {
  margin: 20px 0;
  padding: 0;
  list-style-type: none;
}

.products__item {
  border: 2px dashed #ec005a;
  margin: 10px 0;
  padding: 10px;
}

.products__item:hover {
  background-color: #fff600;
}

